<template>
    <div class="form">
        <form action="" method="get">
            <li>
                <span>  : </span>
                <input type="text" placeholder=",//..." class="name" name="juese">
            </li>
            <li>
                <span>:</span>
                <el-button type="warning" plain style="margin-left: 25px" @click="addroot()"></el-button>
                <div class="roots">
                    <button type="button" id="allchose" @click="allchose()"></button>
                    <button type="button" id="allnot" @click="allnot()"></button>
                    <button type="button" id="fanxuan" @click="fanxuan()"></button><br>
                    <input type="checkbox" value="1" name="rinfo"> <br>
                    <input type="checkbox" value="2" name="rinfo"> <br>
                    <input type="checkbox" value="3" name="rinfo"> <br>
                    <input type="checkbox" value="4" name="rinfo"> <br>
                    <input type="checkbox" value="5" name="rinfo"> <br>
                    <input type="checkbox" value="6" name="rinfo"> <br>
                    <input type="checkbox" value="7" name="rinfo"> <br>
                    <input type="checkbox" value="8" name="rinfo"> <br>
                    <input type="checkbox" value="9" name="rinfo"> <br>
                    <button type="button" class="tijiao" @click="tijiao()"></button>
                </div>
            </li>
            <li>
                <input type="button" value="" class="submit" @click="upup()">
                <input type="reset" value="" class="reset">
            </li>
        </form>
    </div>
</template>

<script>
      export default {
          name: "addJuese",
          data(){
            return {
                input:'',
              }
          },
          methods:{
              addroot(){
                  $('.roots').show(100)
              },
              tijiao(){
                  $('.roots').hide(100)
              },
              allchose(){
                  $("input[name='rinfo']:checkbox").each(function(){
                      $(this).prop("checked", true);
                  });
              },
              allnot(){
                  $("input[name='rinfo']:checkbox").each(function(){
                      $(this).prop("checked", false);
                  });
              },
              fanxuan(){
                  $("input[name='rinfo']:checkbox").each(function(){
                      if($(this).is(':checked')) {
                          $(this).prop("checked", false);
                      }else {
                          $(this).prop("checked", true);
                      }
                  });
              },
              upup(){
                  let name=$('.name').val()
                  let roots=[]
                  $("input[name='rinfo']:checkbox").each(function(){
                      if($(this).is(':checked')) {
                            roots.push($(this).val())
                        }
                  })
                  roots=roots.join("|")
                  fetch('/ajax/juese/jueseadd?name='+name+'&roots='+roots).then(function (e) {
                        return e.text()
                    }).then((e)=>{
                        if (e=='ok'){
                            alert('')
                        }
                    });
              }
          }
      }
</script>

<style scoped>
    .form{
        width: 1100px;
        height: 460px;
        margin: 130px auto 0;
        border: 1px solid #ccc;
    }
    form{
        width: 600px;
        height: 460px;
        background: #ccc;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        position: relative;
    }
    .name{
        width: 230px;
        height: 35px;
        margin-left: 20px;
        border-radius: 2px;
        border: 0;
        padding-left: 10px;
    }
    .reset,.submit{
        width: 70px;
        height: 40px;
        border: 0;
        border-radius: 3px;
        background: orange;
        margin-left: 30px;
    }
    .submit{
        background: green;
    }
    .roots{
        display: none;
        width: 200px;
        height: auto;
        border: 1px solid #333;
        background: #fff;
        border-radius: 5px;
        position: absolute;
        right: 15px;
        bottom: 120px;
    }
    #allchose{
        margin-right: 15px;
    }
    #allnot{
        margin-right: 15px;
    }
</style>